import React, { useState, useEffect } from "react";
import { inject, observer } from "mobx-react";
import classnames from "classnames";
import DeriNumberFormat from "../../utils/DeriNumberFormat";
import "./leaderboard.less";
import Icon from "../../components/Icon/Icon";
import { gql } from "graphql-request";
import { graph_request_test } from "../../utils/graph_api";
import { bg, formatAddress } from "../../utils/utils";

export interface Props {
  wallet: any;
}
function Leaderboard({ wallet = {} }: Props) {
  const [rankingsList, setRankingsList] = useState<any>([]);
  const [graphqlList, setGraphqlList] = useState<any>([]);
  const [accountDays, setAccountDays] = useState(30);
  const [listDays, setListDays] = useState(30);
  const [accountInfo,setAccountInfo] = useState<any>({})
  const timeDay: number = 86400;
  const loadList = async () => {
    let query = gql`
      query ($chainId: Int!) {
        accountDailyPnl(where: { chainId: $chainId }) {
          chainId
          account
          fee
          notional
          pnl
          timestamp
        }
      }
    `;
    let variables = { chainId: 56 };
    let data = await graph_request_test(query, variables);
    if (data.accountDailyPnl.length) {
      let list = data.accountDailyPnl;
      setGraphqlList(list);
    }
  };

  const filterList = (graphqlList,Days)=>{
    const timestamp = timeDay * Days;
      let timestampNow = new Date();
      let timestampBegin = parseInt(
        String(timestampNow.getTime() / 1000 - timestamp - 1)
      );
      let timeArr = graphqlList.filter(
        (item) => item.timestamp > timestampBegin
      );
      let accountArr = timeArr.map((item) => {
        return item.account;
      });
      let account = [...new Set(accountArr)];
      let pnlList = account.map((item) => {
        let obj = {
          account: item,
          fee: 0,
          notional: 0,
          pnl: 0,
        };
        let accountPnlArr = timeArr.filter((fItem) => fItem.account === item);
        let pnl = accountPnlArr.map((item) => {
          obj["pnl"] = bg(obj["pnl"]).plus(item.pnl).toNumber();
          obj["fee"] = bg(obj["fee"]).plus(item.fee).toNumber();
          obj["notional"] = bg(obj["notional"]).plus(item.notional).toNumber();
        });
        return obj;
      });
      return pnlList
  }
  useEffect(() => {
    loadList();
  }, []);
  useEffect(() => {
    if (graphqlList.length) {
      let pnlList = filterList(graphqlList,listDays)
      let list = pnlList.sort((a, b) => b.pnl - a.pnl);
      list = list.splice(0,50)
      setRankingsList(list);
    }
  }, [listDays, graphqlList]);
  useEffect(() => {
    if (graphqlList.length && wallet.account) {
      let pnlList = filterList(graphqlList,accountDays)
      let account = pnlList.find((item)=> item.account === wallet.account)
      if(account){
        setAccountInfo(account)
      }
    }
  }, [accountDays, graphqlList,wallet.chainId, wallet.account]);
  return (
    <div className="Leaderboard">
      <div className="Leaderboard-box">
        <div className="my-historical-trading">
          <div className="title-tab-day">
            <div className="title-leaderboard">
              My Historical Trading Performance
            </div>
            <div className="tab-day">
              <div
                className={classnames("day-btn", {
                  active: accountDays === 70,
                })}
                onClick={() => {
                  setAccountDays(70);
                }}
              >
                70 DAYS
              </div>
              <div
                className={classnames("day-btn", {
                  active: accountDays === 30,
                })}
                onClick={() => {
                  setAccountDays(30);
                }}
              >
                30 DAYS
              </div>
            </div>
          </div>
          <div className="my-info-list">
            <div className="info-box">
              <div className="info-num"> <DeriNumberFormat prefix="$ "  value={accountInfo.notional} decimalScale={2} /></div>
              <div className="info-title"> Notional </div>
            </div>
            <div className="info-box">
              <div className="info-num"><DeriNumberFormat prefix="$ " value={accountInfo.fee} decimalScale={2} /></div>
              <div className="info-title"> Transaction Fee </div>
            </div>
            <div className="info-box">
              <div className="info-num"> <DeriNumberFormat prefix="$ " value={accountInfo.pnl} decimalScale={2} /></div>
              <div className="info-title"> PNL </div>
            </div>
          </div>
        </div>
        <div className="rankings">
          <div className="rankings-title-tab-day">
            <div className="rankings-title">Rankings</div>
            <div className="tab-day">
              <div
                className={classnames("day-btn", { active: listDays === 70 })}
                onClick={() => {
                  setListDays(70);
                }}
              >
                70 DAYS
              </div>
              <div
                className={classnames("day-btn", { active: listDays === 30 })}
                onClick={() => {
                  setListDays(30);
                }}
              >
                30 DAYS
              </div>
            </div>
          </div>
          <div className="rankings-list-title">
            <div className="rank">Rank</div>
            <div className="trade">Trade</div>
            <div className="notional">Notional</div>
            <div className="transaction-fee">Transaction Fee</div>
            <div className="pnl">PNL</div>
          </div>
          <div className="rankings-list-num">
            {rankingsList.map((item, index) => {
              return (
                <div className="rankings-list-num-box" key={index}>
                  <div className="rank">
                    {index + 1 === 1 && (
                      <Icon
                        token="topOne"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 === 2 && (
                      <Icon
                        token="topTwo"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 === 3 && (
                      <Icon
                        token="topThree"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 > 3 && (
                      <span className="top-num">{index + 1}</span>
                    )}
                  </div>
                  <div className="trade">{formatAddress(item.account)}</div>
                  <div className="notional">
                    ${" "}
                    <DeriNumberFormat
                      value={item.notional}
                      displayType="text"
                      decimalScale={2}
                    />
                  </div>
                  <div className="transaction-fee">
                    ${" "}
                    <DeriNumberFormat
                      value={item.fee}
                      displayType="text"
                      decimalScale={2}
                    />
                  </div>
                  <div
                    className={classnames("pnl", {
                      up: +item.pnl > 0,
                      down: +item.pnl < 0,
                    })}
                  >
                    {+item.pnl > 0 ? "+" : ""} 
                    <DeriNumberFormat
                      value={item.pnl}
                      displayType="text"
                      decimalScale={2}
                      prefix="$ "
                      // allowZero={true}
                    />
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
export default inject("wallet")(observer(Leaderboard));